@font-face {
  font-family: BPDots;
  src: url("BPdotsSquareVF.25fec6ee.ttf") format("truetype");
  font-display: swap;
}

@font-face {
  font-family: ZxGamut;
  src: url("ZxGamut-Variable.6528b92f.woff2") format("woff2");
  font-display: swap;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  background: #80b0ef;
  min-height: 100vh;
  font-family: ZxGamut, monospace;
  overflow-x: hidden;
}

.app {
  z-index: 10;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  min-height: 100vh;
  padding: 2rem;
  display: flex;
  position: relative;
  overflow-y: auto;
}

#p5-background {
  z-index: 1;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

#p5-background canvas {
  opacity: 0;
  transition: opacity 1.5s ease-in-out;
  display: block;
}

#p5-background canvas.fade-in {
  opacity: 1;
}

#background-video {
  object-fit: cover;
  z-index: 0;
  width: 100%;
  height: 100%;
  image-rendering: pixelated;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
  position: fixed;
  top: 0;
  left: 0;
}

.landing-container {
  text-align: center;
  width: 100%;
  max-width: 900px;
  margin-bottom: 2rem;
}

.hero-section {
  border-bottom: 1px solid #ffffff1a;
  padding: 3rem;
}

.hero-text {
  color: #0d1b33;
  margin: 2rem 0;
  font-size: 1.3rem;
}

.content-sections {
  flex-direction: column;
  gap: 2rem;
  padding: 0;
  display: flex;
}

.content-section {
  backdrop-filter: blur(8px);
  text-align: left;
  color: #0d1b33;
  background: #ffffff4d;
  border: 1px solid #fff3;
  border-radius: 8px;
  padding: 2rem;
}

.content-section * {
  color: inherit;
}

.content-section strong {
  color: #0d1b33;
}

.title-section {
  backdrop-filter: blur(8px);
  text-align: center;
  background: #ffffff4d;
  border: 1px solid #fff3;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
  padding: 2rem;
  display: flex;
}

.subtitle-section {
  backdrop-filter: blur(8px);
  text-align: center;
  background: #ffffff4d;
  border: 1px solid #fff3;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
  padding: 1.5rem;
  display: flex;
}

.description-section {
  backdrop-filter: blur(8px);
  text-align: center;
  background: #ffffff4d;
  border: 1px solid #fff3;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
  padding: 2rem;
  display: flex;
}

.program-details {
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin-top: 1rem;
  margin-bottom: 0;
  display: grid;
}

.detail-item {
  backdrop-filter: blur(8px);
  color: #0d1b33;
  background: #ffffff4d;
  border: 1px solid #fff3;
  border-radius: 8px;
  padding: 1rem;
}

.detail-item strong {
  color: #0d1b33;
}

.curriculum-grid {
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin-top: 1rem;
  margin-bottom: 0;
  display: grid;
}

.curriculum-block {
  backdrop-filter: blur(8px);
  background: #ffffff4d;
  border: 1px solid #fff3;
  border-radius: 8px;
  padding: 1.5rem;
}

.curriculum-block h3 {
  color: #1e4a6b;
  margin-bottom: .5rem;
  font-family: BPDots, monospace;
  font-size: 1.1rem;
  font-weight: 900;
}

.who-list {
  color: #0d1b33;
  margin: 1rem 0 0;
  padding: 0;
  list-style: none;
}

.who-list li {
  color: #0d1b33;
  margin-bottom: .5rem;
  padding: .5rem 0 .5rem 1.5rem;
  position: relative;
}

.who-list li:before {
  content: "→";
  color: #1e4a6b;
  font-size: 1.2rem;
  font-weight: bold;
  position: absolute;
  left: 0;
}

.diversity-note {
  color: #0d1b33cc;
  border-top: 1px solid #ffffff4d;
  margin-top: 1.5rem;
  padding: 1.5rem 0 1rem;
  font-style: italic;
}

.team-grid {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-top: 1rem;
  margin-bottom: 0;
  display: grid;
}

.team-member {
  backdrop-filter: blur(8px);
  background: #ffffff4d;
  border: 1px solid #fff3;
  border-radius: 8px;
  padding: 1.5rem;
}

.team-photo-placeholder {
  aspect-ratio: 1;
  color: #fff9;
  background: #ffffff1a;
  border: 1px solid #ffffff4d;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 1rem;
  font-family: ZxGamut, monospace;
  display: flex;
}

.team-photo {
  aspect-ratio: 1;
  object-fit: cover;
  border-radius: 8px;
  width: 100%;
  margin-bottom: 1rem;
}

.team-member h3 {
  color: #0d1b33;
  margin-bottom: .75rem;
  font-family: BPDots, monospace;
  font-size: 1.2rem;
  font-weight: 900;
}

.partners-grid {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
  margin-top: 1rem;
  margin-bottom: 0;
  display: grid;
}

.partner-item {
  text-align: center;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.partner-logo-placeholder {
  color: #0d1b3399;
  background: #ffffff1a;
  border: 1px solid #ffffff4d;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100px;
  margin-bottom: 1rem;
  font-family: ZxGamut, monospace;
  display: flex;
}

.partner-logo {
  object-fit: contain;
  border-radius: 8px;
  width: 100%;
  max-width: 200px;
  height: 100px;
  margin-bottom: 1rem;
}

.partner-item h3 {
  color: #0d1b33;
  margin: 0;
  font-family: BPDots, monospace;
  font-size: 1.1rem;
  font-weight: 900;
}

.partner-item h3 a {
  color: #1e4a6b;
  text-decoration: none;
  transition: color .3s;
}

.partner-item h3 a:hover {
  color: #0d1b33;
  text-decoration: underline;
}

.carousel-container {
  max-width: 100%;
  margin: 1rem 0 0;
  padding-bottom: 4rem;
  position: relative;
}

.carousel-track {
  border-radius: 8px;
  min-height: 400px;
  margin-bottom: 3rem;
  position: relative;
}

.carousel-item {
  opacity: 0;
  flex-direction: column;
  width: 100%;
  height: 100%;
  transition: opacity .5s ease-in-out;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.carousel-item.active {
  opacity: 1;
}

.carousel-image-placeholder {
  color: #0d1b3399;
  background: #ffffff1a;
  border: 1px solid #ffffff4d;
  border-radius: 8px;
  flex: 1;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
  font-family: ZxGamut, monospace;
  display: flex;
}

.carousel-image {
  object-fit: contain;
  box-sizing: border-box;
  background: #ffffff1a;
  border-radius: 8px;
  max-width: 100%;
  height: auto;
  max-height: 400px;
  margin: 0 auto 1rem;
  padding: .75rem;
  display: block;
}

.carousel-caption {
  text-align: center;
  color: #0d1b33cc;
  margin: 0;
  padding: 0;
  font-style: italic;
}

.carousel-caption a {
  color: #1e4a6b;
  text-decoration: none;
  transition: color .3s;
}

.carousel-caption a:hover {
  color: #0d1b33;
  text-decoration: underline;
}

.carousel-credit {
  text-align: center;
  color: #0d1b3399;
  margin: .5rem 0 0;
  padding: 0;
  font-size: .8rem;
}

.carousel-credit a {
  color: #1e4a6b;
  text-decoration: none;
  transition: color .3s;
}

.carousel-credit a:hover {
  color: #0d1b33;
  text-decoration: underline;
}

.carousel-controls {
  justify-content: space-between;
  align-items: center;
  padding: -.5rem;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.carousel-btn {
  backdrop-filter: blur(8px);
  color: #0d1b33;
  cursor: pointer;
  background: #ffffff4d;
  border: 1px solid #fff3;
  border-radius: 8px;
  padding: .5rem 1rem;
  font-family: BPDots, monospace;
  font-weight: 900;
  transition: all .3s;
}

.carousel-btn:hover {
  background: #ffffff80;
}

.carousel-indicators {
  gap: .5rem;
  display: flex;
}

.indicator {
  cursor: pointer;
  background: #ffffff4d;
  border: 1px solid #fff3;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  transition: all .3s;
}

.indicator.active {
  background: #1e4a6b99;
  border-color: #1e4a6bcc;
}

.contact-section {
  text-align: center;
  backdrop-filter: blur(8px);
  background: #ffffff4d;
  border-radius: 8px;
  padding: 2rem;
  border: 1px solid #fff3 !important;
}

.contact-section a {
  color: #1e4a6b;
  font-weight: bold;
  text-decoration: none;
}

.contact-section a:hover {
  color: #0d1b33;
  text-decoration: underline;
}

.applications-section {
  backdrop-filter: blur(8px);
  text-align: center;
  border-radius: 8px;
  margin-bottom: 2rem;
  background: #ffffff4d !important;
  border: 1px solid #1e4a6b4d !important;
}

.signup-form {
  flex-direction: column;
  gap: 1rem;
  margin-top: 1.5rem;
  display: flex;
}

.form-group {
  flex-direction: column;
  display: flex;
}

.form-row {
  gap: 1rem;
  display: flex;
}

.form-row input {
  flex: 1;
}

.signup-form input, .signup-form textarea {
  backdrop-filter: blur(8px);
  color: #0d1b33;
  resize: none;
  background: #ffffff4d;
  border: 1px solid #fff3;
  border-radius: 8px;
  padding: .75rem;
  font-family: ZxGamut, monospace;
  font-size: 1rem;
}

.signup-form input::placeholder, .signup-form textarea::placeholder {
  color: #0d1b3399;
}

.signup-form input:focus, .signup-form textarea:focus {
  border-color: #1e4a6b99;
  outline: none;
}

.signup-button {
  backdrop-filter: blur(8px);
  color: #1e4a6b;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 1px;
  background: #ffffff4d;
  border: 2px solid #1e4a6b80;
  border-radius: 8px;
  padding: 1rem;
  font-family: BPDots, monospace;
  font-size: 1rem;
  font-weight: 900;
  transition: border-color .3s, color .3s;
}

.signup-button:hover {
  color: #0d1b33;
  border-color: #1e4a6bcc;
}

h1 {
  color: #0d1b33;
  margin-bottom: 1rem;
  font-family: BPDots, monospace;
  font-size: 5rem;
  font-weight: 900;
}

h2 {
  color: #0d1b33;
  margin-bottom: 2rem;
  font-family: BPDots, monospace;
  font-size: 2rem;
  font-weight: 900;
}

.title-section h1 {
  margin: 0;
  transform: translateY(.1em);
}

.subtitle-section h2 {
  color: #0d1b33;
  margin: 0;
  transform: translateY(.1em);
}

.description-section .hero-text {
  margin: 0;
}

p {
  color: #0d1b33;
  margin-bottom: 1rem;
  font-family: ZxGamut, monospace;
  font-size: 1.2rem;
  line-height: 1.6;
}

p:last-child {
  margin-bottom: 0;
}

.cta-button {
  color: #0d1b33;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 2px;
  background: #000000e6;
  border: 2px solid #ffffff80;
  border-radius: 8px;
  padding: 1rem 2rem;
  font-family: BPDots, monospace;
  font-size: 1.2rem;
  font-weight: 900;
  transition: transform .3s, border-color .3s;
}

.cta-button:hover {
  border-color: #fffc;
  transform: translateY(-2px);
}

@media (width <= 768px) {
  .app {
    padding: 1rem 1rem .5rem;
  }

  h1 {
    font-size: 2.5rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  p {
    font-size: 1rem;
  }

  .title-section, .subtitle-section, .description-section {
    margin-bottom: 1rem;
    padding: 1rem;
  }

  .content-section {
    padding: 1rem;
  }

  .content-sections {
    gap: 1rem;
  }

  .applications-section {
    margin-bottom: 1rem;
  }

  .program-details, .curriculum-grid, .team-grid {
    grid-template-columns: 1fr;
  }

  .form-row {
    flex-direction: column;
    gap: 1rem;
  }

  .partners-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .carousel-track {
    min-height: 300px;
    margin-bottom: 0;
  }

  .carousel-image {
    max-height: 300px;
  }

  .carousel-controls {
    flex-direction: row;
    gap: 0;
  }

  .carousel-btn {
    padding: .5rem;
    font-size: .9rem;
  }

  .carousel-credit {
    padding-bottom: 1rem;
  }
}
